<template>
  <div class="">
    <img class="captcha" :src="src" @click="switchCaptcha">
  </div>
</template>

<script type="text/ecmascript-6">
  export default {
    data() {
      return {
        src: `/node_common/captcha?${new Date().getTime()}`,
        publicKey: ''
      }
    },
    created() {
      this.switchCaptcha()
    },
    methods: {
      switchCaptcha() {
        this.src = `/node_common/captcha?${new Date().getTime()}`
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .captcha {
    width: 160px;
    height: 100%;
  }
</style>



